import { Button } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import PropTypes from "prop-types";
import React from "react";
import {
  makeStyles,
  FormControl,
  Select,
  InputLabel,
  MenuItem,
} from "@material-ui/core";
import { closePopup } from "../../state/actions/popups";
import { getUserTables, deleteUserTable } from "../../state/actions/popups";

const useStyles = makeStyles({
  popupbox: {
    position: "absolute",
    background: "#00000050",
    width: "100%",
    height: "100vh",
    top: 0,
    left: 0,
  },
  box: {
    position: "relative",
    width: "70%",
    margin: "0 auto",
    height: "auto",
    maxHeight: "70vh",
    marginTop: "calc(15vh - 20px)",
    background: "white",
    borderRadius: "4px",
    padding: "20px",
    border: "1px solid black",
    overflow: "auto",
  },
  boxik: {
    display: "flex",
    color: "black",
    background: "white",
    fontSize: "1em",
    whiteSpace: "wrap",
    textTransform: "none",
    borderRadius: "5px",
    borderColor: "black",
    borderWidth: "1px",
    margin: "10px 10px",
    borderStyle: "solid",
    padding: "5px",
  },
  columns: {
    display: "flex",
  },
  closeicon: {
    content: "x",
    cursor: "pointer",
    position: "fixed",
    right: "calc(15% - 12.5px)",
    top: "calc(15vh - 32.5px)",
    background: "white",
    width: "25px",
    height: "25px",
    borderRadius: "50%",
    lineHeight: "20px",
    textAlign: "center",
    border: "1px solid black",
    fontSize: "20px",
    fontWeight: "bold",
  },
  makeEmptyButton: {
    border: "1px solid black",
    color: "white",
    background: "blue",
    marginTop: "10px",
    marginRight: "20px",
  },
  button: {
    border: "1px solid black",
    background: "green",
    color: "white",
    marginTop: "10px",
  },
  formControl: {
    display: "flex",
    marginBottom: "10px",
    minWidth: 200,
  },
  header: {
    margin: "10px 0px",
  },
  deleteButton: {
    marginTop: "10px",
    background: "red",
    color: "white",
    padding: "10px 20px",
    fontWeight: "bold",
    marginLeft: "auto",
    fontSize: "20px",
    "&:hover": {
      background: "black",
    },
  },
});

const UserTablesPopup = () => {
  const [shouldUpdate, setShouldUpdate] = React.useState(false);
  const [selectedUser, setSelectedUser] = React.useState(undefined);
  const [oldUserTables, setOldUserTables] = React.useState([]);
  const users = useSelector((store) => store.user.users);
  const userTables = useSelector((store) => store.popups.userTables);

  const classes = useStyles();
  const dispatch = useDispatch();

  React.useEffect(() => {
    if (userTables.length !== oldUserTables.length) {
      setShouldUpdate(!shouldUpdate);
      setOldUserTables(userTables);
    }
  }, [userTables.length, oldUserTables.length, userTables, shouldUpdate]);

  const handleChange = (event) => {
    setSelectedUser(event.target.value);
    dispatch(getUserTables(event.target.value));
  };

  const handleClose = () => {
    dispatch(closePopup());
  };

  const handleDelete = (item) => {
    dispatch(deleteUserTable(item.id, selectedUser.id));
  };

  return (
    <div className={classes.popupbox}>
      <div className={classes.box}>
        <span className={classes.closeicon} onClick={handleClose}>
          x
        </span>
        <h2 className={classes.header}>Delete User Tables</h2>
        {shouldUpdate && <span></span>}
        <div>
          <FormControl className={classes.formControl}>
            <InputLabel id="label">User</InputLabel>
            <Select
              id="demo-simple-select-helper"
              value={selectedUser || ""}
              label="User"
              onChange={handleChange}
            >
              {users.map((user, ix) => (
                <MenuItem key={ix} value={user}>
                  <em>{user.email}</em>
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </div>
        {userTables &&
          userTables.map((item) => (
            <UserTablesListItem
              item={item}
              user={users.find((e) => e.id === item.userId)}
              key={userTables.indexOf(item)}
              onDelete={handleDelete}
            />
          ))}
      </div>
    </div>
  );
};
export default UserTablesPopup;

const UserTablesListItem = ({ item, onDelete }) => {
  const classes = useStyles();

  const deleteTable = () => {
    onDelete(item);
  };

  return (
    <div className={classes.boxik}>
      <div>
        <span className={classes.columns}>Table Name: {item.name}</span>
        <span className={classes.columns}>Table ID: {item.id}</span>
        <span className={classes.columns}>Table Order: {item.tableOrder}</span>
      </div>
      <Button className={classes.deleteButton} onClick={deleteTable}>
        X
      </Button>
    </div>
  );
};
UserTablesListItem.propTypes = {
  item: PropTypes.object,
  onDelete: PropTypes.func,
};
