export function deleteUserTable(tableId, userId) {
  return async (dispatch) => {
    try {
      db.collection("users")
        .doc(userId)
        .collection("tables")
        .doc(tableId)
        .delete();
      dispatch({ type: DELETE_USER_TABLE_SUCCESS, tableId });
    } catch (e) {
      dispatch({ type: DELETE_USER_TABLE_FAILED });
    }
  };
}

export function getUserTables(user) {
  return async (dispatch) => {
    dispatch({ type: GET_USER_TABLES });
    try {
      db.collection("users")
        .doc(user.id)
        .collection("tables")
        .get()
        .then((snapshot) => {
          const data = [];
          snapshot.forEach((snap) => {
            data.push({ ...snap.data(), id: snap.id });
          });
          dispatch({ type: GET_USER_TABLES_SUCCESS, data });
        });
    } catch (e) {
      dispatch({ type: GET_USER_TABLES_FAILED });
    }
  };
}
