import PropTypes from "prop-types";
import { Button, makeStyles } from "@material-ui/core";
import { useSelector, useDispatch } from "react-redux";

import EmptyCell from "./cells/EmptyCell";
import Cell from "./cells/Cell";
import { addSelectingRowCells } from "../../state/actions/cellsRows";

const useStyles = makeStyles({
  row: {
    display: "table-row",
  },
  selectRowButton: {
    background: "blue",
    fontWeight: "bold",
    color: "white",
    width: "50px",
    height: "50px",
    border: "1px solid white",
    "&:hover": {
      color: "black",
    },
  },
});

const CellsRow = ({ cellsRow }) => {
  const classes = useStyles();
  const cellToUpdateCustomCells = useSelector(
    (store) => store.popups.cellToUpdateCustomCells
  );
  const selectedCells = useSelector((store) => store.cellsRows.selectedCells);
  const cellsRowLetsUpdate = useSelector((store) => store.cellsRows.letsUpdate);
  const isSelecting = useSelector((store) => store.cellsRows.isSelecting);
  const letsUpdate = useSelector((store) => store.popups.letsUpdate);
  const dispatch = useDispatch();

  const selectRow = () => {
    dispatch(addSelectingRowCells(cellsRow.rowIndex));
  };

  return (
    <div className={classes.row}>
      {letsUpdate && cellsRowLetsUpdate && <div></div>}
      {isSelecting && (
        <Button onClick={selectRow} className={classes.selectRowButton}>
          {">"}
        </Button>
      )}
      {cellsRow &&
        cellsRow.cells &&
        cellsRow.cells
          .sort((a, b) => (a.columnIndex > b.columnIndex ? 1 : -1))
          .map((cell) =>
            cell.type === "empty" || cell.type === "image" ? (
              <EmptyCell
                cell={cell}
                cellsRow={cellsRow}
                key={`${cellsRow.rowIndex}` + cell.columnIndex}
              />
            ) : (
              <Cell
                cell={cell}
                cellsRow={cellsRow}
                isCustomCell={
                  (cellToUpdateCustomCells &&
                    cellToUpdateCustomCells.includes(cell)) ||
                  (selectedCells && selectedCells.includes(cell))
                }
                key={`${cellsRow.rowIndex}` + cell.columnIndex}
              />
            )
          )}
    </div>
  );
};
CellsRow.propTypes = {
  cellsRow: PropTypes.object,
};
export default CellsRow;
