import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import {
  showActionPopup,
  showInfoPopup,
  showNoTypePopup,
  cellToUpdateCustomCellSelected,
} from "../../../state/actions/popups";
import { addSelectingCell } from "../../../state/actions/cellsRows";
import dao from "./dao.png";

const useStyles = makeStyles({
  cell: {
    background: (props) => props.color,
    borderLeftWidth: (props) => props.left + "px",
    borderRightWidth: (props) => props.right + "px",
    borderTopWidth: (props) => props.top + "px",
    borderBottomWidth: (props) => props.bottom + "px",
    borderTopColor: (props) =>
      props.isCustomCell
        ? "#2BFF00"
        : props.borderColor === undefined ||
          props.borderColor === null ||
          props.borderColor === ""
        ? "black"
        : props.borderColor,
    borderRightColor: (props) =>
      props.isCustomCell
        ? "#2BFF00"
        : props.borderColor === undefined ||
          props.borderColor === null ||
          props.borderColor === ""
        ? "black"
        : props.borderColor,
    borderLeftColor: (props) =>
      props.isCustomCell
        ? "#2BFF00"
        : props.borderColor === undefined ||
          props.borderColor === null ||
          props.borderColor === ""
        ? "black"
        : props.borderColor,
    borderBottomColor: (props) =>
      props.isCustomCell
        ? "#2BFF00"
        : props.borderColor === undefined ||
          props.borderColor === null ||
          props.borderColor === ""
        ? "black"
        : props.borderColor,
    height: "50px",
    width: "100px",
    borderLeftStyle: "solid",
    borderRightStyle: "solid",
    borderTopStyle: "solid",
    borderBottomStyle: "solid",
    alignContent: "center",
    textAlign: "center",
    justifyContent: "center",
    display: "table-cell",
    verticalAlign: "middle",
    lineHeight: "0.87",
  },
});

const Cell = ({ cell, cellsRow, isCustomCell }) => {
  const classes = useStyles({
    left: cell.leftBorder,
    right: cell.rightBorder,
    top: cell.topBorder,
    bottom: cell.bottomBorder,
    color: cell.color,
    isCustomCell,
    borderColor: cell.borderColor,
  });
  const cellToUpdate = useSelector((store) => store.popups.cellToUpdate);
  const isSelecting = useSelector((store) => store.cellsRows.isSelecting);

  const dispatch = useDispatch();

  const action = () => {
    if (isSelecting) {
      dispatch(addSelectingCell(cell));
    } else if (cellToUpdate !== undefined) {
      if (
        !(
          cellToUpdate.cell.columnIndex === cell.columnIndex &&
          cellToUpdate.cell.rowIndex === cell.rowIndex
        ) &&
        (cell.type === "info" || cell.type === "action")
      ) {
        dispatch(cellToUpdateCustomCellSelected(cell));
      }
    } else if (cell.type === "notype") {
      dispatch(showNoTypePopup(cell, cellsRow));
    } else if (cell.type === "info") {
      dispatch(showInfoPopup(cell, cellsRow));
    } else if (cell.type === "action") {
      dispatch(showActionPopup(cell, cellsRow));
    }
  };

  return (
    <div className={classes.cell} onClick={action}>
      {cell.sumAll && (
        <img
          style={{
            width: "25px",
            height: "25px",
            marginLeft: "auto",
            marginRight: "auto",
          }}
          src={dao}
          alt="all"
        />
      )}
      {cell.name && !cell.sumAll && (
        <span
          style={{
            fontSize:
              cell.fontSize === undefined || cell.fontSize === null
                ? 12
                : cell.fontSize + "px",
            fontWeight: cell.name.includes("<strong>") ? "bold" : "normal",
            fontStyle: cell.name.includes("<em>") ? "italic" : "normal",
            textDecoration: cell.name.includes("<u>") ? "underline" : "none",
            color:
              cell.fontColor === undefined ||
              cell.fontColor === null ||
              cell.fontColor === ""
                ? "black"
                : cell.fontColor,
          }}
        >
          {cell.name
            .replaceAll("<p>", "")
            .replaceAll("</p>", "")
            .replaceAll("<span>", "")
            .replaceAll("</span>", "")
            .replaceAll("&amp;", "")
            .replaceAll("&nbsp;", "")
            .replaceAll(`<span class="ql-cursor">`, "")
            .replaceAll("<em>", "")
            .replaceAll("</em>", "")
            .replaceAll("<u>", "")
            .replaceAll("</u>", "")
            .replaceAll("<strong>", "")
            .replaceAll("</strong>", "")}
        </span>
      )}
    </div>
  );
};
Cell.propTypes = {
  cell: PropTypes.object,
  cellsRow: PropTypes.object,
  isCustomCell: PropTypes.bool,
};
export default Cell;
